*{
  margin: 0;
  padding: 0;
}

.App{
  background: #2e2933;
  height: 100vh;
  font-family: Courier, monospace;
  color:#e3e3e3;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.home .title{
  font-size: 24px;
}

/* CSS */
.playBtn {
  align-items: center;
  appearance: none;
  background-color: #FCFCFD;
  border-radius: 4px;
  border-width: 0;
  box-shadow: rgba(45, 35, 66, 0.4) 0 2px 4px,rgba(45, 35, 66, 0.3) 0 7px 13px -3px,#D6D6E7 0 -3px 0 inset;
  box-sizing: border-box;
  color: #36395A;
  cursor: pointer;
  display: inline-flex;
  font-family: "JetBrains Mono",monospace;
  height: 24px;
  justify-content: center;
  line-height: 1;
  list-style: none;
  overflow: hidden;
  padding-left: 16px;
  padding-right: 16px;
  position: relative;
  text-align: left;
  text-decoration: none;
  transition: box-shadow .15s,transform .15s;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  white-space: nowrap;
  will-change: box-shadow,transform;
  font-size: 12px;
  margin-top: 1.5%;
}

.playBtn:focus {
  box-shadow: #D6D6E7 0 0 0 1.5px inset, rgba(45, 35, 66, 0.4) 0 2px 4px, rgba(45, 35, 66, 0.3) 0 7px 13px -3px, #D6D6E7 0 -3px 0 inset;
}

.playBtn:hover {
  box-shadow: rgba(45, 35, 66, 0.4) 0 4px 8px, rgba(45, 35, 66, 0.3) 0 7px 13px -3px, #D6D6E7 0 -3px 0 inset;
  transform: translateY(-2px);
}

.playBtn:active {
  box-shadow: #D6D6E7 0 3px 7px inset;
  transform: translateY(2px);
}

.wordWrapper {
    padding: 32px;
    height: 72px;
    max-width: 800px;
    overflow: hidden;
}

.words {
  display: flex;
  flex-wrap: wrap;
}

.word {
  position: relative;
  font-size: 1em;
  line-height: 1em;
  margin: .25em;
  font-variant: no-common-ligatures;
  border-bottom: 2px solid transparent;
}

.correct {
  color: #588559
}

.incorrect {
  color : #aa5050
}

.inputForm {
  position: relative;
  top:-100px;
  z-index: 1000;
  width: 100%;
  height: 100%;
}

input {
  width: 90%;
  height: 100px;
  opacity: 0;
}
